.notes {
  position: relative;
  padding-bottom: 48px;
  &__decor {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  &__title {
    position: relative;
    margin-bottom: 32px;
    text-align: center;
    font-size: 24px;
    font-weight: 500;
    line-height: 32px;
  }
  &__slider-box {
    position: relative;
  }
  &__slider {
  }
  &__slider-item {
    position: relative;
    height: 360px;
    border-radius: 32px;
    border: 1px solid var(--Line-Light, #eaeaea);
    background: rgba(255, 255, 255, 0.9);
    backdrop-filter: blur(8px);
  }
}
